html, body, #root, .App{
  height:100%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*Common styles*/
.AppHeader{
  width:100%;
  font-weight:bold;
  font-size: calc(10px + 2vmin);
  padding:1%;
  color: #fafafa;
  background-color: #282c34;
  cursor:pointer;
}

.AppHeader span {
  font-size: 0.5em;
  font-weight: normal;
  display: block;
  padding: 0.2%;
}

