@media only screen and (max-width: 400px) {
  .login{
    width:70%;
    height:40%;
  }
}

@media only screen and (min-width: 401px) and (max-width: 960px) {
  .login{
    width:50%;
    height:30%;
  }
}



/*-----------------------Location page styles-------------------------*/
.locationPage{
  background : url('./assets/index.jpg') no-repeat;  /*background short-hand*/
}

.homePage form{
  display:flex;
  align-items:center;
  justify-content:center;
  height:100%;
  background : url('./assets/index.jpg') no-repeat;  /*background short-hand*/
  background-size : cover;
}

.homePage input[type="text"]{
  padding:0.5%;
}
.homePage input[type="submit"]{
  padding: 0.5% 0 0.5% 0;
  cursor: pointer;
}

/*Restaurant page styles*/
.restaurantList {
  display:flex;
  flex-wrap:wrap;
  align-items: center;
  justify-content: center;
  background:#fafafa;
}

.restaurantList .restaurant{
  margin:2%;
  min-width:20%;
  cursor: pointer;
  transition : transform .2s;
}

.restaurantList .restaurant .restaurantName{
  width:90%;
}

.restaurant:hover{
  transform: scale(1.1);
}

.restaurantList span{
  font-size: 0.8em;
  color: #403e3e;
}

/*Restaurant details page styles*/
.RestaurantDetails{
  display:flex;
  justify-content: center;
  flex-wrap:wrap;
  background:#fafafa;
}

.RestaurantDetails .img-section {
  width:55%;
  padding:2% 2% 2% 5%;
  background:#efeeee;
  margin-top:2%;
}

.RestaurantDetails .img-section img{
  max-width : 80%;
  transition : transform .2s;
}

.RestaurantDetails .img-section img:hover{
  transform: scale(1.09);
}

.RestaurantDetails .details-section{
  width:30%;
  padding: 5% 0 2% 2%;
  background:#efeeee;
  margin-top:2%;
}

.RestaurantDetails .details-section > span {
  font-size: 0.8em;
  display:block;
  margin:1.5%;
}

.RestaurantDetails .details-section .rest-type{
  background:grey;
  padding:1%;
  background: #d6d2cc;
  border-radius: 25%;
}
